.explorerContainer{
    background: rgb(41, 41, 41);
    position: absolute;
    z-index: inherit;
    top: 30px;
    left: 1px;
    right: 1px;
    bottom: 1px;

    .toolBar{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        height: 30px;
        width: 100%;
        background-color: black;
    }

    .iconGrid{    
        margin: 5px;
        justify-content: start;
        position: relative;
        display: grid;
        grid-gap: 5px;
        grid-template-columns: repeat(auto-fit, 55px);
        grid-template-rows: repeat(auto-fit, 65px);
        background: rgb(41, 41, 41);

        top: 0px;
        left: 0px;
        right: 0px;
        bottom: 1px;
    
    }
}

