.taskBar{
    z-index: 5;

    position: fixed;
    bottom: 0px;
    height: 35px;
    width: 100%;
    background: rgb(41, 41, 41);
    flex-direction: row;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .startBtn{
        left: 0px;
        width: 46px;
        height: 100%;
        border: 0px;
        background: rgb(41, 41, 41);

        display: flex;
        justify-content: center;
        align-items: center;
        img{
            width: 25px;
            height: 25px;
        }
    }
    .taskBarItems{
        height: 90%;
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: start;
        overflow: hidden;
        .taskBarItem{
            height: 100%;
            margin: 0px 2px;
            border-bottom: 2px solid green;
            background: transparent;
            transition: background-color 0.2s ease;
            width: 150px;
            display: flex;
            flex-direction: row;
            align-items: center;
            img{
                height: 80%;
            }
            label{
                color: white;
                font-size: 12px;
                font-weight: 100;
            }
            
        }        
        :hover{
            background: rgba(255, 255, 255, 0.109);
            opacity: 100%;
        }
        .selected{
            background: rgba(255, 255, 255, 0.198);
        }
        

    }
    .toolBar{
        right: 0px;
        height: 100%;

        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
        
        .minimizeAllBtn{
            height: 100%;
            width: 5px;
            background: rgb(41, 41, 41);
            border: 0px;
            border-left: gray 1px solid;
        }

        .timeContainer{
            margin: 0px 15px 0px 15px;
        }
    }
}